import React from 'react';

import Turista from "../../../assests/image/Turista.png";
import Comercio from "../../../assests/image/Comercio.png";
import Service from "../../../assests/image/Service.png";
import './Style.css';

const Destiny = () =>{
     return(
        <section id="destiny">
            <div>
              <h2>A Quem se Destina?</h2>
            </div>
            <div className="destiny">
              <div className="card">
                <div>
                  <img src={Turista} alt="imagem de um icone de usario" />
                  <span>Turista</span>
                </div>
                <p>
                  Se voc  algum que gosta de viajar e conhecer diferentes
                  lugares, visitar pontos tursticos e tirar diversas fotos,
                  aqui  possvel. Voc pode localizar mais facilmente os pontos
                  turisticos atravs das avaliaes de outros usurios e no
                  perde tempo ao procurar por conta prpria.
                </p>
              </div>
              <div className="card marginCard">
                <div>
                  <img src={Comercio} alt="imagem de um icone de comrcio" />
                  <span>Comrcio</span>
                </div>
                <p>
                  Se voc  do negcio e recebe turistas diariamente, aqui voc
                  pode divulgar seu comrcio tornando mais fcil das pessoas
                  encontrarem e avaliarem.
                </p>
              </div>
              <div className="card">
                <div>
                  <img
                    src={Service}
                    alt="imagem de um icone servio publico "
                  />
                  <span>Servio pblico</span>
                </div>
                <p>
                  Se voc  do Servio Pblico e deseja fomentar o turismo em
                  seu municpio, aqui voc cria o seu mapa turstico digital,
                  identificando seus principais pontos a serem visitados na
                  cidade.
                </p>
              </div>
            </div>
          </section>
     )
}

export default Destiny